/*! Flickity v2.0.5
http://flickity.metafizzy.co
---------------------------------------------- */
.flickity-enabled {
  position: relative;
}

.flickity-enabled:focus { outline: none; }

.flickity-viewport {
  overflow: hidden;
  position: relative;
  height: 100%;
}

.flickity-slider {
  position: absolute;
  width: 100%;
  height: 100%;
}

/* draggable */
.flickity-enabled.is-draggable {
  tap-highlight-color: transparent;
  user-select: none;
}

.flickity-enabled.is-draggable .flickity-viewport {
  cursor: move;
  cursor: grab;
}

.flickity-enabled.is-draggable .flickity-viewport.is-pointer-down {
  cursor: grabbing;
}

/* ---- previous/next buttons ---- */
.flickity-prev-next-button {
  position: absolute;
  top: 50%;
  width: 80px;
  height: 80px;
  border: none;
  border-radius: 50%;
  background: rgba($white, 0.2);
  transform: translateY(-50%);
  cursor: pointer;
  .main__equipment &,
  .product__similar & {
    display: none;
    width: 50px;
    height: 50px;
    background: rgba($black, 0.2);
    @media (min-width: 768px) {
      display: block;
    }
  }
  .gallery__slider & {
    background-color: transparent;
  }
  .gallery__slider-main & {
    top: auto;
    bottom: -100px;
    z-index: 1;
    @media (min-width: 768px) {
      display: none;
    }
  }
  .gallery__slider-nav & {
    @media (max-width: 767px) {
      display: none;
    }
  }
}

.flickity-prev-next-button:hover {
  background: rgba($white, 0.5);
  .main__equipment &,
  .product__similar & {
    background: rgba($black, 0.5);
  }
  .gallery__slider & {
    background: transparent;
  }
}

.flickity-prev-next-button:focus {
  outline: none;
  box-shadow: 0 0 0 5px #09F;
}

.flickity-prev-next-button:active {
  opacity: 0.6;
}

.flickity-prev-next-button.previous {
  left: -40px;
  .main__equipment &,
  .product__similar & {
    left: -25px;
  }
}
.flickity-prev-next-button.next {
  right: -40px;
  .main__equipment &,
  .product__similar & {
    right: -25px;
  }
}

.flickity-prev-next-button:disabled {
  opacity: 0.3;
  cursor: auto;
}

.flickity-prev-next-button svg {
  position: absolute;
  top: 20%;
  width: 30px;
  height: 60%;
  .main__equipment &,
  .product__similar & {
    width: 10px;
  }
  .gallery__slider & {
    width: 14px;
  }
}

.flickity-prev-next-button {
  &.previous svg {
    right: 0;
    .main__equipment &,
    .product__similar &,
    .gallery__slider & {
      right: 10px;
    }
  }
  &.next svg {
    left: 0;
    .main__equipment &,
    .product__similar &,
    .gallery__slider & {
      left: 10px;
    }
  }
}

.flickity-prev-next-button .arrow {
  fill: $white;
  .gallery__slider & {
    fill: #999;
  }
}

/* ---- page dots ---- */
.flickity-page-dots {
  position: absolute;
  width: 100%;
  bottom: -20px;
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center;
  line-height: 1;
  z-index: 1;
  @media (min-width: 768px) {
    bottom: 23px;
  }
  .cards & {
    width: calc(100% - 30px);
    bottom: -20px;
  }
  .main__equipment &,
  .product__similar & {
    @media (min-width: 768px) {
      display: none;
    }
  }
}

.flickity-page-dots .dot {
  display: inline-block;
  width: 24px;
  height: 4px;
  margin: 0 5px;
  background: #ccc;
  border-radius: 2px;
  opacity: 0.5;
  cursor: pointer;
  @media (min-width: 768px) {
    background: $white;
  }
}

.flickity-page-dots .dot.is-selected {
  background-color: $red;
  opacity: 1;
}
