.table {
  @media (min-width: 768px) {
    .tabs__item & {
      height: 270px;
    }
    .tabs__item--noscroll & {
      height: auto;
    }
  }
  &__row {
    @media (min-width: 768px) {
      display: flex;
    }
    &:nth-child(odd) {
      @media (min-width: 768px) {
        background-color: #f5f5f5;
      }
    }
    &:nth-child(even) {
      @media (max-width: 767px) {
        background-color: #f5f5f5;
      }
    }
    > div {
      padding-left: 20px;
      padding-right: 20px;
      font-size: 13px;
      @media (min-width: 768px) {
        padding: 11px 18px;
        font-size: 14px;
      }
      &:nth-child(1) {
        color: #666;
        @media (max-width: 767px) {
          padding-top: 14px;
        }
        @media (min-width: 768px) {
          width: 60%;
        }
      }
      &:nth-child(2) {
        color: #333;
        @media (max-width: 767px) {
          padding-top: 4px;
          padding-bottom: 14px;
        }
        @media (min-width: 768px) {
          width: 40%;
        }
      }
      &._right {
        @media (min-width: 768px) {
          text-align: right;
        }
      }
    }
  }
}


.content-table {
  position: relative;
  &__wrapper {
    overflow-x: auto;
  }
  &__icon {
    position: absolute;
    right: -10px;
    bottom: -22px;
    width: 45px;
    height: 45px;
    background-color: $red;
    border-radius: 50%;
    @media (min-width: 768px) {
      display: none;
    }
  }
  &__body {
    font-size: 12px;
    border-spacing: 0;
    border-collapse: collapse;
    @media (min-width: 768px) {
      font-size: 14px;
    }
    th {
      vertical-align: top;
      padding: 15px;
      color: #fff;
      background-color: #1a1a1a;
      font-weight: 700;
      text-align: left;
      border: 1px solid #000;
      &._active {
        background-color: $red;
        border: 1px solid $red;
      }
    }
    td {
      padding: 15px;
      color: #333;
      border: 1px solid #dcdcdc;
      &:first-child {
        border-right: 0;
      }
      &._active {
        border-left: 1px solid $red;
        border-right: 1px solid $red;
      }
    }
    tr:nth-child(odd) {
      background-color: #f5f5f5;
      td:first-child {
        background-color: #ccc;
      }
    }
    tr:nth-child(even) {
      td:first-child {
        background-color: #e0e0e0;
      }
    }
    tr:nth-child(2) {
      td:first-child {
        .gallery__table & {
          background-color: #fff;
        }
      }
    }
    tr:last-child td._active {
      border-bottom: 1px solid $red;
    }
  }
}
