.r-tabs .r-tabs-nav {
  display: flex;
  margin: 0;
  padding: 0;
}
.r-tabs .r-tabs-tab {
  display: block;
  width: 100%;
  max-width: 170px;
  font-size: 14px;
  text-align: center;
  border-top: 1px solid #dcdcdc;
  border-left: 1px solid #dcdcdc;
  &:last-child {
    border-right: 1px solid #dcdcdc;
  }
  &:hover {
    background-color: #dcdcdc;
    a {
      color: #333;
    }
  }
  a {
    display: block;
    padding: 13px 0;
    color: #666;
    span {
      margin-left: 5px;
      color: $red;
    }
  }
}
.r-tabs .r-tabs-tab.r-tabs-state-active {
  background-color: $red;
  border: 0 none;
  a {
    color: $white;
    span {
      color: $white;
    }
  }
}
.r-tabs .r-tabs-panel {
  display: none;
}

.r-tabs .r-tabs-accordion-title {
  display: none;
  position: relative;
  background-color: #dcdcdc;
  font-size: 14px;
  &::after {
    position: absolute;
    content: "";
    top: 50%;
    right: 13px;
    width: 14px;
    height: 8px;
    margin-top: -4px;
    background: svg-load('assets/images/icons/icon-menu-arrow.svg');
    background-size: 14px 8px;
    transition: transform $fast;
  }
  &.r-tabs-state-active::after {
    transform: rotate(180deg);
  }
  a {
    display: block;
    padding: 17px 20px;
    color: #333;
  }
}
.r-tabs .r-tabs-panel.r-tabs-state-active {
  display: block;
}

@media only screen and (max-width: 768px) {
  .r-tabs .r-tabs-nav {
    display: none;
  }
  .r-tabs .r-tabs-accordion-title {
    display: block;
  }
}
